@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;500&family=Roboto:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  margin-top: -1rem;
}

.App {
  text-align: center;
  background-color: #FAF5CA;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App button {
  display: inline;
  margin-inline: 1.25rem;
  width: 180px;
  height: 48px;
  font-weight: 300;
  font-family: 'Inter', sans-serif;
  background-color: #2F82FF;
  border: none;
  border-radius: 20px;
  color: #fff;
  text-decoration: none;
  font-size: 20px;
}

.App button:hover {
  cursor: pointer;
  background-color: #1e447d;
  transition: 0.4s;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.App a {
  text-decoration: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  margin-bottom: 108px;
}

.title {
  width: 664px;
  height: 201px;
}

.App-link {
  color: #61dafb;
}

.buttons {
  margin: 1.5rem;
}

@-webkit-keyframes bounce {
  0%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
}

@keyframes bounce {
  0%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
}

.App-logo {
  line-height: 100px;
  -webkit-animation: bounce 4s infinite;
          animation: bounce 4s infinite;
}

.App-logo-pages {
    width: 136px;
    height: 120px;
    box-shadow: 0 0 90px 2px rgba(0, 0, 0, 50%);
    border-radius: 10%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transition: 0.5s;
}

.App-logo-pages:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    box-shadow: none;
    transition: 0.5s;
    border: 1px solid #d08c10;
    cursor: pointer;
}

.title-pages {
    width: 615px;
    height: 145px;
    margin-inline: 200px;
    margin-left: 100px;
}

.engrenagem {
    width: 66px;
    height: 66px;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transition: .5s;
}

.engrenagem:hover {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    transition: .5s;
    cursor: pointer;
}

.App-header-pages {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.main-image {
    width: 500px;
    height: 280px;
    border-radius: 5%;
}

